import Vue from 'vue'
// 
//--
Vue.filter('yuan', function(value) {
    return (value / 100).toFixed(2)
})
//--
Vue.filter('yuan_z', function(value) {
    return (value / 100).toFixed(0)
})
//
Vue.filter('percent_f', function(value) {
    return value+'%'
})



